.search .form-actions::after,
.search .form-actions:hover::after {
  display: inline-block;
  background-image: url("../img/sprite.svg");
  background-size: 521px 497px;
}

.search {
  display: flex;
  align-items: center;
  position: absolute;
  right: 0;
}

.search:hover .form-item {
  width: 816px;
  opacity: 1;
}

.search form {
  display: flex;
  width: 100%;
}

.search .form-item {
  transition: all 0.25s ease-in-out;
  width: 0;
  flex: 0 0 auto;
  margin-right: -16px;
  opacity: 0;
}

.search .form-text {
  height: 107px;
}

.search .form-text:focus {
  box-shadow: none;
}

.search .form-actions {
  margin: 0;
  background-color: #fff;
  position: absolute;
  top: 1px;
  right: 1px;
  bottom: 1px;
  border-radius: 3px;
}

.search .form-actions::after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  z-index: 0;
  width: 23px;
  height: 24px;
  background-position: -241px -331px;
}

.search .form-actions:hover::after {
  width: 23px;
  height: 24px;
  background-position: -209px -331px;
}

.search .form-submit {
  position: relative;
  width: 47px;
  height: 100%;
  border: none;
  padding: 0;
  transition: background-color 0.25s ease-in-out;
  background-color: transparent;
  text-indent: -999999px;
  z-index: 1;
  cursor: pointer;
}

.search_focused .form-item {
  width: 816px;
  opacity: 1;
}

@media only screen and (max-width: 1199px) {
  .search:hover .form-item {
    width: 620px;
  }

  .search_focused .form-item {
    width: 620px;
  }
}